"use client";
import { useEffect, useState, useRef } from "react";
import { auth, db, storage  } from "../../firebase";
import {
  collection,
  query,
  where,
  onSnapshot,
  orderBy,
  addDoc,
  doc,
  setDoc,
  getDocs,
  deleteDoc,
} from "firebase/firestore";
import { signOut, onAuthStateChanged } from "firebase/auth";
import styles from "../../styles/chat.module.css";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { updateProfile } from "firebase/auth"; 



export default function ChatApp() {
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const [currentScreen, setCurrentScreen] = useState("chat");
  const [theme, setTheme] = useState("light");
  const [font, setFont] = useState("Arial");
  const [fontSize, setFontSize] = useState(16);
  const [nametagInput, setNametagInput] = useState("");
  const [contactEmail, setContactEmail] = useState("");
  const [showUserOptions, setShowUserOptions] = useState({});
  const [isMobile, setIsMobile] = useState(false);
  const [showSidebar, setShowSidebar] = useState(true);

  const user = auth.currentUser;
  const messagesEndRef = useRef(null);

  const [currentUser, setCurrentUser] = useState(null);

  // Detecta se est√° no mobile
  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth <= 768);
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (!user) window.location.href = "/login";
      else setCurrentUser(user);
    });
    return () => unsubscribe();
  }, []);

  // Carrega contatos
  useEffect(() => {
    if (!user) return;
    const q = query(collection(db, "contacts"), where("ownerId", "==", user.uid));
    const unsub = onSnapshot(q, (snapshot) => {
      const contatos = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setUsers(contatos);
    });
    return unsub;
  }, [user]);

  // Carrega mensagens
  useEffect(() => {
    if (!selectedUser || !user) return;
    const messagesRef = collection(db, "messages");
    const q = query(messagesRef, orderBy("timestamp", "asc"));
    const unsub = onSnapshot(q, (snapshot) => {
      const msgs = snapshot.docs
        .map((doc) => doc.data())
        .filter(
          (msg) =>
            (msg.senderId === user.uid && msg.receiverId === selectedUser.uid) ||
            (msg.senderId === selectedUser.uid && msg.receiverId === user.uid)
        );
      setMessages(msgs);
      scrollToBottom();
    });
    return unsub;
  }, [selectedUser, user]);

  // Fecha o menu dos 3 pontinhos ao clicar fora
useEffect(() => {
  const handleClickOutside = (event) => {
    // Verifica se o clique foi fora de qualquer menu aberto
    if (!event.target.closest(`.${styles.userOptionsContainer}`)) {
      setShowUserOptions({});
    }
  };

  document.addEventListener("click", handleClickOutside);
  return () => document.removeEventListener("click", handleClickOutside);
}, []);

useEffect(() => {
  const savedTheme = localStorage.getItem("voxTalkTheme");
  if (savedTheme) {
    setTheme(savedTheme);
  }
}, []);

useEffect(() => {
  if (!user) return;

  const q = query(collection(db, "users"));
  const unsub = onSnapshot(q, (snapshot) => {
    const usersData = snapshot.docs.map(doc => ({ uid: doc.data().uid, ...doc.data() }));
    // Atualiza contatos com dados mais recentes
    setUsers(prev =>
      prev.map(contact => {
        const updatedUser = usersData.find(u => u.uid === contact.contactId);
        return updatedUser ? { ...contact, ...updatedUser } : contact;
      })
    );
  });

  return unsub;
}, [user]);


useEffect(() => {
  const container = document.getElementById("chatMessagesContainer");
  if (container) {
    container.style.fontFamily = font;
    container.style.fontSize = `${fontSize}px`;
  }
}, [font, fontSize, messages]); // Atualiza sempre que mudar fonte, tamanho ou mensagens
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const handleSendMessage = async () => {
    if (!newMessage.trim() || !selectedUser) return;
    await addDoc(collection(db, "messages"), {
      senderId: user.uid,
      receiverId: selectedUser.uid,
      text: newMessage,
      timestamp: new Date(),
    });
    setNewMessage("");
  };

  const handleLogout = () => signOut(auth).then(() => (window.location.href = "/login"));
 const handleThemeChange = () => {
  const newTheme = theme === "light" ? "dark" : "light";
  setTheme(newTheme);
  localStorage.setItem("voxTalkTheme", newTheme); // salva no navegador
};
  const handleFontChange = (e) => setFont(e.target.value);
  const increaseFont = () => setFontSize((prev) => Math.min(prev + 2, 30));
  const decreaseFont = () => setFontSize((prev) => Math.max(prev - 2, 12));

  // Adicionar contato
  const handleAddContact = async () => {
    if (!contactEmail.trim() && !nametagInput.trim()) {
      return alert("Preencha o E-mail ou a Nametag do contato.");
    }

    let q;
    if (contactEmail.trim()) {
      q = query(collection(db, "users"), where("email", "==", contactEmail.trim()));
    } else {
      q = query(collection(db, "users"), where("nametag", "==", nametagInput.trim()));
    }

    const snapshot = await getDocs(q);
    if (snapshot.empty) return alert("Usu√°rio n√£o encontrado.");

    const contactUser = snapshot.docs[0].data();

    const existingContact = users.find((u) => u.contactId === contactUser.uid);
    if (existingContact) return alert("Contato j√° adicionado.");

    await addDoc(collection(db, "contacts"), {
      ownerId: user.uid,
      contactId: contactUser.uid,
      nametag: contactUser.nametag,
      photoURL: contactUser.photoURL || "",
      email: contactUser.email,
    });

    alert("Contato adicionado!");
    setContactEmail("");
    setNametagInput("");
    setCurrentScreen("chat");
  };

  const handleDeleteConversation = async () => {
    if (!selectedUser) return;
    const messagesRef = collection(db, "messages");
    const snapshot = await getDocs(messagesRef);
    snapshot.forEach(async (docu) => {
      const msg = docu.data();
      if (
        (msg.senderId === user.uid && msg.receiverId === selectedUser.uid) ||
        (msg.senderId === selectedUser.uid && msg.receiverId === user.uid)
      ) {
        await deleteDoc(doc(db, "messages", docu.id));
      }
    });
    setMessages([]);
    alert("Conversa apagada!");
  };

  const handleDeleteContact = async (contactId) => {
    const contactDocRef = doc(db, "contacts", contactId);
    await deleteDoc(contactDocRef);
    if (selectedUser?.uid === contactId) setSelectedUser(null);
    alert("Contato removido!");
  };

  // üîπ fun√ß√£o que muda de tela e esconde sidebar no mobile
  const navigateTo = (screen) => {
    setCurrentScreen(screen);
    if (isMobile) setShowSidebar(false);
  };

  return (
    <div
      className={`${styles.container} ${theme === "dark" ? styles.darkTheme : ""}`}
     style={{ fontFamily: 'Arial' }}
    >
      {(!isMobile || showSidebar) && (
        <div className={styles.sidebar}>
          <div className={styles.userProfile}>
           
            <div>
              <strong>{user?.displayName}</strong>
              <p>{user?.nametag}</p>
            </div>
          </div>

          <ul className={styles.userList}>
            {users.map((u) => (
              <li
                key={u.id}
                className={selectedUser?.uid === u.contactId ? "selected" : ""}
                onClick={() => {
                  setSelectedUser({
                    uid: u.contactId,
                    nametag: u.nametag,
                    photoURL: u.photoURL,
                  });
                  setCurrentScreen("chat");
                  if (isMobile) setShowSidebar(false);
                }}
              >
                <img
   src={localImages[user.uid] || "/images/default.jpg"} 
  style={{ borderRadius: "50%", width: 50, height: 50 }}
  alt="Foto do usu√°rio"
/>

                <span>{u.nametag}</span>

                <div className={styles.userOptionsContainer}>
                  <span
  className={styles.userDots}
  onClick={(e) => {
    e.stopPropagation();

    setShowUserOptions((prev) => {
      // Fecha todos se clicar no mesmo ou em outro
      if (prev[u.id]) return {};
      return { [u.id]: true };
    });
  }}
>
  ‚ãÆ
</span>


                  {showUserOptions[u.id] && (
                    <div className={styles.userButtonsPopup}>
                      <button onClick={handleDeleteConversation}>
                        Apagar conversa
                      </button>
                      <button onClick={() => handleDeleteContact(u.id)}>
                        Apagar contato
                      </button>
                    </div>
                  )}
                </div>
              </li>
            ))}
          </ul>
{/* VoxTalk logo e bot√µes */}
          <div className={styles.sidebarLogo}>
            <h3>üí¨ VoxTalk</h3>
          </div>
          <div className={styles.sidebarButtons}>
           
            <button onClick={() => navigateTo("profile")}>Perfil</button>
            <button onClick={() => navigateTo("config")}>Configura√ß√µes</button>
            <button onClick={() => navigateTo("voice")}>Chat de voz</button>
            <button onClick={() => navigateTo("addContact")}>Adicionar contato</button>
            <button onClick={handleLogout}>Sair da conta</button>
          </div>
        </div>
      )}

      {(!isMobile || !showSidebar) && (
        <div className={styles.main}>
          {isMobile && (
            <button
              onClick={() => setShowSidebar(true)}
              style={{
                background: "transparent",
                border: "none",
                fontSize: "20px",
                margin: "10px",
              }}
            >
              ‚Üê Voltar
            </button>
          )}

         {currentScreen === "chat" && selectedUser && (
  <>
    {/* Cabe√ßalho do chat permanece igual */}
    <div className={styles.chatHeader}>
      <img
   src={localImages[user.uid] || "/images/default.jpg"} 
  style={{ borderRadius: "50%", width: 50, height: 50 }}
  alt="Foto do usu√°rio"
/>

      <span className={styles.contactName}>{selectedUser.nametag}</span>
    </div>

    {/* Mensagens - aplicar s√≥ aqui */}
    <div
    id="chatMessagesContainer"
      className={styles.messages}
      style={{ fontFamily: font, fontSize: `${fontSize}px` }}
    >
      {messages.map((msg, idx) => (
        <div
          key={idx}
          className={`${styles.message} ${
            msg.senderId === user.uid ? styles.sent : styles.received
          }`}
        >
          {msg.text}
          <span className={styles.messageTime}>
            {new Date(msg.timestamp.seconds * 1000).toLocaleTimeString([], {
              hour: "2-digit",
              minute: "2-digit",
            })}
          </span>
        </div>
      ))}
      <div ref={messagesEndRef}></div>
    </div>

    {/* Input do chat permanece igual */}
    <div className={styles.inputContainer}>
      <input
        value={newMessage}
        onChange={(e) => setNewMessage(e.target.value)}
        onKeyDown={(e) => e.key === "Enter" && handleSendMessage()}
        placeholder="Digite sua mensagem..."
      />
      <button onClick={handleSendMessage}>Enviar</button>
    </div>
  </>
)}


          {currentScreen === "config" && (
            <div className={styles.configScreen}>
              <h2>Configura√ß√µes</h2>
              <div className={styles.centeredFieldset}>
                <button
                  className={styles.roundedButton}
                  onClick={handleThemeChange}
                >
                  Tema: {theme}
                </button>

                <label>Fonte:</label>
                <select
                  value={font}
                  onChange={handleFontChange}
                  className={styles.inputField}
                >
                  <option>Arial</option>
                  <option>Verdana</option>
                  <option>Tahoma</option>
                  <option>Courier New</option>
                </select>

                <label>Tamanho da mensagem:</label>
                <div style={{ display: "flex", gap: "10px" }}>
                  <button
                    className={styles.roundedButton}
                    onClick={decreaseFont}
                  >
                    -
                  </button>
                  <span>{fontSize}px</span>
                  <button
                    className={styles.roundedButton}
                    onClick={increaseFont}
                  >
                    +
                  </button>
                </div>
                <div
  style={{
    marginTop: "15px",
    padding: "10px",
    border: "1px solid #ccc",
    borderRadius: "8px",
    fontFamily: font,
    fontSize: `${fontSize}px`,
  }}
>
  Exemplo de mensagem no chat
</div>
              </div>
            </div>
          )}

          {currentScreen === "profile" && <ProfileScreen user={user} />}

          {currentScreen === "addContact" && (
            <div className={styles.addContactScreen}>
              <h2>Adicionar Contato</h2>
              <fieldset className={styles.centeredFieldset}>
                <legend>Informa√ß√µes do Contato</legend>
                <input
                  placeholder="Email ou Nametag"
                  value={contactEmail || nametagInput}
                  onChange={(e) => {
                    const val = e.target.value;
                    if (val.includes("@")) setContactEmail(val);
                    else setNametagInput(val);
                  }}
                />
                <button
                  className={styles.roundedButton}
                  onClick={handleAddContact}
                >
                  Adicionar
                </button>
              </fieldset>
            </div>
          )}

          {currentScreen === "voice" && (
            <div className={styles.voiceScreen}>
              <h2>Chat de Voz (WebRTC)</h2>
              <p>A implementa√ß√£o b√°sica de WebRTC pode ser adicionada aqui.</p>
            </div>
          )}
        </div>
      )}
    </div>
  );
}
// Mapeamento dos usu√°rios para imagens locais
const localImages = {
  "kKHiBhhFMuUqTqJigmAApOZl2xJ3": "/images/1.jpg",
  "gJdfyLOUrSct4fZen8V0L2xPwU52": "/images/2.jpg",
  "N2lnBPZXg4PUq49kcJdFNWa1fnX2": "/images/3.jpg",
};


function ProfileScreen({ user }) {
  const [newNametag, setNewNametag] = useState(user?.nametag || "");
  const [photoURL, setPhotoURL] = useState(user?.photoURL || "");
  const [loading, setLoading] = useState(false);

  const checkNametagExists = async (nametag) => {
    const q = query(collection(db, "users"), where("nametag", "==", nametag));
    const snapshot = await getDocs(q);
    return !snapshot.empty && snapshot.docs[0].data().uid !== user.uid;
  };

 

const handleUpdate = async () => {
  if (!newNametag.trim()) return alert("Preencha a nametag");

  setLoading(true);

  try {
    const user = auth.currentUser;

   await updateProfile(user, {
  displayName: newNametag.trim(),
  photoURL: photoURL || "https://via.placeholder.com/80",
});

// Atualiza Firestore
const userRef = doc(db, "users", user.uid);
await setDoc(userRef, {
  nametag: newNametag.trim(),
  photoURL: photoURL || "https://via.placeholder.com/80",
}, { merge: true });

// Atualiza contatos
const contactsRef = collection(db, "contacts");
const q = query(contactsRef, where("contactId", "==", user.uid));
const contactsSnapshot = await getDocs(q);

contactsSnapshot.forEach(async (docu) => {
  await setDoc(doc(db, "contacts", docu.id), {
    nametag: newNametag.trim(),
    photoURL: photoURL || "https://via.placeholder.com/80",
  }, { merge: true });
});


    alert("Perfil atualizado!");
  } catch (error) {
    console.error("Erro ao atualizar perfil:", error);
    alert("Erro ao atualizar o perfil. Tente novamente.");
  } finally {
    setLoading(false);
  }
};




  return (
    <div className={styles.profileScreen}>
      <h2>Perfil</h2>
      <div className={styles.centeredFieldset}>
        <img
   src={localImages[user.uid] || "/images/default.jpg"} 
  style={{ borderRadius: "50%", width: 100, height: 100 }}
  alt="Foto do usu√°rio"
/>

        <p>
          <strong>{user?.displayName}</strong>
        </p>
        <p>{user?.email}</p>
        <p>
          <strong>Nametag:</strong> {user?.nametag}
        </p>

        <input
          placeholder="Nova Nametag"
          value={newNametag}
          onChange={(e) => setNewNametag(e.target.value)}
        />
        <p>
  <strong>Foto de Perfil:</strong>
</p>
<input
  type="file"
  accept="image/*"
  onChange={async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    setLoading(true); // opcional: bloqueia bot√£o enquanto faz upload
    try {
      // Cria refer√™ncia no Storage
      const storageRef = ref(storage, `profilePhotos/${user.uid}/${file.name}`);
      
      // Envia arquivo
      await uploadBytes(storageRef, file);
      
      // Pega URL de download
      const url = await getDownloadURL(storageRef);
      
      // Atualiza estado local e exibe na tela
      setPhotoURL(url);
    } catch (err) {
      console.error(err);
      alert("Erro ao enviar a foto.");
    } finally {
      setLoading(false);
    }
  }}
/>

        <button
          className={styles.roundedButton}
          onClick={handleUpdate}
          disabled={loading}
        >
          {loading ? "Atualizando..." : "Atualizar Perfil"}
        </button>
      </div>
    </div>
  );
}
